<template>
    <div class="whoAreWeContainer">
        <h1>Wer sind wir?</h1>
        <h2>Wir sind eine Software Agentur die Unternehmen, in den Bereich Web-, App Entwicklung, Marketing und in vielen anderen Bereichen unterstützt.</h2>
        <h2>Wir bieten Ihnen Expertise, Tools, Infrastruktur, Markenentwicklung und professionellen Service an, die Ihnen helfen erfolgreich zu sein und sich zu unterscheiden.</h2>
    </div>
</template>

<script>
export default {
    
    methods: {
        scrollReveal() {
            if(window.pageYOffset > document.querySelector('.whoAreWeContainer').offsetTop - (window.innerHeight/3)) {
                document.querySelector('.whoAreWeContainer').classList.add('show-whoAreWeContainer')
            }
        },
    },

    mounted() {
        window.addEventListener('scroll', this.scrollReveal)
    },

    beforeDestroy() {
        window.removeEventListener('scroll', this.scrollReveal)
    },
}
</script>

<style lang="sass">
@import '@/assets/style/variables.sass'

.whoAreWeContainer
    background: $themeGradient
    width: 100vw
    height: 100vh
    display: flex
    flex-direction: column
    justify-content: center
    align-items: center
    h1, h2
        opacity: 0
        transform: translateY(30px)
        margin-top: 20px
        color: #fff
        text-align: center
        width: 40%
        transition: all 1s ease
        @media (max-width: $tabletWidth)
            width: 80%
            font-size: 21px
    h2
        transition-delay: .5s
        font-size: 20px
        @media (max-width: $tabletWidth)
            font-size: 16px

.show-whoAreWeContainer
    h1, h2
        opacity: 1 !important
        transform: translateY(0) !important

</style>